import {ElementRef} from '@angular/core';

export interface InteractPanel {
  /**
   * Panel
   */
  panel: ElementRef<HTMLDivElement>;

  /**
   * Navigation
   */
  nav: ElementRef<HTMLDivElement>;

  /**
   * Open panel
   */
  isOpened: boolean;

  /**
   * Position. Can be 'left' or 'right'
   */
  position: string;

  /**
   * Panel width
   */
  panelWidth: string;
}
